import React, { useEffect, useState } from "react";


const Header = (props) => {
    useEffect(()=>{

    })

    return <div className="header">
        {props.parent==="about"?
        <>        
        <h2 id="label-font" className="gallery-content">{props.bool?"Personal gallery: ":"Лична галерия: "}</h2>
        <div className="header-choices">
            <a id="label-font" className="pictures-redirect" href="#/pictures">{props.bool?"Digital art":"Дигитални картини"}</a>
            <a id="label-font" className="pictures-redirect" href="#/drawings">{props.bool?"Traditional art":"Традиционни картини"}</a>
            <a id="label-font" className="models-redirect" href="#/models">{props.bool?"3D models":"3D модели"}</a>
            <a id="label-font" className="animations-redirect" href="#/animations">{props.bool?"Animations":"Анимации"}</a>
        </div>
        </>
        :
        <>
        </>}

    </div>
}

export default Header;